
'use server';
/**
 * @fileOverview A speech-to-text AI flow using Google Cloud Speech-to-Text.
 *
 * - speechToText - A function that converts audio to text.
 * - SpeechToTextInput - The input type for the speechToText function.
 * - SpeechToTextOutput - The return type for the speechToText function.
 */

import { ai } from '@/ai/genkit';
import { z } from 'genkit';
import { SpeechClient } from '@google-cloud/speech';

const SpeechToTextInputSchema = z.object({
  audioDataUri: z
    .string()
    .describe(
      "The audio to transcribe, as a data URI that must include a MIME type and use Base64 encoding. Expected format: 'data:<mimetype>;base64,<encoded_data>'."
    ),
  languageCode: z.string().optional().describe('The language of the audio. Defaults to en-US.'),
});
export type SpeechToTextInput = z.infer<typeof SpeechToTextInputSchema>;

const SpeechToTextOutputSchema = z.object({
  transcript: z.string().describe('The transcribed text.'),
});
export type SpeechToTextOutput = z.infer<typeof SpeechToTextOutputSchema>;

export async function speechToText(input: SpeechToTextInput): Promise<SpeechToTextOutput> {
  return speechToTextFlow(input);
}

const speechToTextFlow = ai.defineFlow(
  {
    name: 'speechToTextFlow',
    inputSchema: SpeechToTextInputSchema,
    outputSchema: SpeechToTextOutputSchema,
  },
  async ({ audioDataUri, languageCode = 'en-US' }) => {
    const speechClient = new SpeechClient();

    const audioBytes = audioDataUri.split(',')[1];

    const audio = {
      content: audioBytes,
    };

    const config = {
      encoding: 'WEBM_OPUS' as const, // MediaRecorder with default settings in Chrome produces webm/opus
      sampleRateHertz: 48000, // Common for webm
      languageCode: languageCode,
    };

    const request = {
      audio: audio,
      config: config,
    };

    try {
        const [response] = await speechClient.recognize(request);
        const transcription = response.results
            ?.map(result => result.alternatives?.[0].transcript)
            .join('\n');

        return {
            transcript: transcription || '',
        };
    } catch (error) {
        console.error('Speech-to-text API error:', error);
        throw new Error('Failed to transcribe audio.');
    }
  }
);
